import { newE2EPage } from '@stencil/core/testing';

describe('search-bar-ui', () => {
  it('renders', async () => {
    const page = await newE2EPage();
    await page.setContent('<presto-search-bar-ui></presto-search-bar-ui>');
    const element = await page.find('presto-search-bar-ui');
    expect(element).toHaveClass('hydrated');
  });
});
